<template>
  <button
    class="zoom"
    :style="{ background: combo ? 'transparent' : color }"
    @click.stop="onBlockClick(index)">
    <i :class="combo ? combo + ' dark-gray' : 'dark-gray'"></i>
  </button>
</template>

<script>
export default {
  name: "Block",
  props: {
    index: Number,
    color: String,
    combo: String
  },
  methods: {
    onBlockClick(index) {
      this.$emit("onBlockClick", index);
    },
  },
};
</script>

<style scoped>
button {
  padding: 0px;
  margin: 0px;
  border-style: none;
  border-radius: 5px;
  width: 100%;
  height: 100%;
}
i {
  z-index: 10;
}
</style>