<template>
  <div>
    <div v-if="!playing" class="center-align-vertical">
      <div style="margin-top: 50%; margin-bottom: 50%;">
        <p>You ran out of clicks :(</p>
        <p>But you completed {{ level }} levels!</p>
        <br>
        <span @click="onStartClick">
          <KeyCap class="btn zoom" letter="start"/>
      </span>
      </div>   
    </div>
    <div v-else>
      <div class="form-inline">
        <p :class="levelCounterClass" style="font-size: 26px; font-style: bold;">level {{ level }}</p>
      </div>
      <div class="form-inline">
        <p style="margin-right: 5px;">color: </p>
        <div
          class="color-to-collect block"
          :style="{ background: colorToCollect }">
        </div>
        <p style="padding-left: 5px" :class="amountCounterClass">{{ amountCollected }}/</p>
        <p>{{ amountToCollect }}</p>
      </div>
      <div class="form-inline">
        <p>clicks: {{ clicks }}</p>
      </div>
      <hr class="game-hr" />
      <div class="form-inline">
        <p>bonus:</p>
        <Bonus
          v-for="bonus in bonuses"
          :amount="bonus.amount"
          :bonus="bonus.bonus"
          :description="bonus.description"
          :color="bonus.color"
          :id="bonus.id"
          v-on:on-bonus-click="onBonusClick"
          :key="bonus.id"
        />
      </div>
      <div class="center">
        <div class="board">
          <Block
            :color="block.color"
            :combo="block.combo"
            :index="block.index"
            v-for="block in blocks"
            :key="block.index"
            @onBlockClick="onBlockClick"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ColorMatchMixin from "@/components/games/colormatch/ColorMatchMixin.js";
import GameData from "@/assets/gamedata/GameData.js";
import Block from "@/components/games/colormatch/Block.vue";
import KeyCap from "@/components/games/KeyCap";
import Bonus from "@/components/games/colormatch/Bonus.vue";

const rows = 9;
const columns = 12;
const transparent = "transparent";

var amountCounterClass = "";
var levelCounterClass = "";
var colorToCollect;
var amountToCollect;
var amountCollected = 0;
var clicks;

var blocksToExplode = [];
var amountCollectedThisRound;
var interval;

var blocks = [];
var bonuses = [];
var level = 1;
var playing = false;

export default {
  components: {
    Block,
    KeyCap,
    Bonus,
  },
  mixins: [ColorMatchMixin],
  data() {
    return {
      blocks,
      processingBlocks: false,
      clicks,
      amountToCollect,
      amountCollected,
      colorToCollect,
      amountCounterClass,
      levelCounterClass,
      level,
      playing,
      bonuses,
    };
  },
  created() {
    this.playing = false;
    this.startGame();
  },
  destroyed() {
    clearInterval(interval);
  },
  methods: {
    onStartClick() {
      this.startGame();
    },
    onBonusClick(bonus) {
      this.useBonus(bonus);
    },
    onBlockClick(index) {
      let clickedBlock = this.blocks[index];
      this.clicks--;
      blocksToExplode = [];
      amountCollectedThisRound = 0;

      if (this.isFullColorBoard()) {
        blocksToExplode = this.blocks;
      } 
      else if (this.isCombo(clickedBlock.combo)) {
        this.doCombo(clickedBlock);
      } 
      else {
        this.lookForMatchingNeighbors(index);

        if (blocksToExplode.length > 2) {

          if (this.isThreeInAColumn(blocksToExplode)) {
            clickedBlock.combo = this.getRowCombo();
          } 
          else if (this.isThreeInARow(blocksToExplode, columns)) {
            clickedBlock.combo = this.getColumnCombo();
          } 
          else {
            clickedBlock.combo = this.getBomb();
          }

          blocksToExplode = blocksToExplode.filter(b=> b.index != clickedBlock.index);
          if (clickedBlock.color == this.colorToCollect) amountCollectedThisRound++;
          clickedBlock.color = transparent;
                 
        } 
        else if (blocksToExplode.length < 2) {
          blocksToExplode = [];
        }
      }

      this.destroyBlocks(blocksToExplode);
      this.evaluateRound();
    },
    raiseLevel() {
      this.level++;

      this.animateLevel();
      this.resetBoard();
      this.setGameRules();
    },
    evaluateRound() {

      if (this.clicks < 0) {
        this.gameOver();
        return;
      }
      
      if (amountCollectedThisRound > 0) {
        this.amountCollected += amountCollectedThisRound;
        this.animateCounter();
      }
      if (this.amountCollected >= this.amountToCollect) {
        this.raiseLevel();
      }       
    },
    gameOver() {
      this.playing = false;
      clearInterval(interval);
    },
    lookForMatchingNeighbors(index) {
      let color = this.blocks[index].color;
      blocksToExplode.push(this.blocks[index]);

      let rightIndex = index + 1;
      let rightBlock = this.blocks[rightIndex];
      if (rightBlock != null && !blocksToExplode.includes(rightBlock)) {
        if (
          this.isSameRow(index, rightIndex, columns) &&
          rightBlock.color == color && rightBlock.color != transparent
        ) {
          this.lookForMatchingNeighbors(rightIndex, color);
        }
      }
      let leftIndex = index - 1;
      let leftBlock = this.blocks[leftIndex];
      if (leftBlock != null && !blocksToExplode.includes(leftBlock)) {
        if (
          this.isSameRow(index, leftIndex, columns) &&
          leftBlock.color == color && leftBlock.color != transparent
        ) {
          this.lookForMatchingNeighbors(leftIndex, color);
        }
      }
      let lowerIndex = index + columns;
      let lowerBlock = this.blocks[lowerIndex];
      if (
        lowerBlock != null &&
        !blocksToExplode.includes(lowerBlock) &&
        lowerBlock.color == color && lowerBlock.color != transparent
      ) {
        this.lookForMatchingNeighbors(lowerIndex, color);
      }
      let upperIndex = index - columns;
      let upperBlock = this.blocks[upperIndex];
      if (
        upperBlock != null &&
        !blocksToExplode.includes(upperBlock) &&
        upperBlock.color == color && upperBlock.color != transparent
      ) {
        this.lookForMatchingNeighbors(upperIndex, color);
      }
    },
    doCombo(block) {
      if (this.isBomb(block.combo)) {
        this.findBlocksWithinBombArea(block.index, block.index);
      } 
      else if (this.isRowCombo(block.combo)) {
        this.findBlocksOnSameRow(block);
      } 
      else if (this.isColumnCombo(block.combo)) {
        this.findBlocksOnSameColumn(block);
      }
    },
    useBonus(clickedBonus) {
      if (this.isResetBonus(clickedBonus.bonus)) {
        this.resetBoard();
      } 
      else if (this.isClickBonus(clickedBonus.bonus)) {
        this.clicks += 5;
      } 
      else {
        this.colorBoard(clickedBonus.color);
      }
      this.bonuses.find((b) => b.id == clickedBonus.id).amount--;
    },
    colorBoard(color) {
      for (let i = 0; i < this.blocks.length; i++) {
        if (this.blocks[i].combo == null) this.blocks[i].color = color;
      }
    },
    destroyBlocks(blocksToDestroy) {
      for (let i = 0; i < blocksToDestroy.length; i++) {
        if (blocksToDestroy[i].color == this.colorToCollect) {
          amountCollectedThisRound++;
        }
        blocksToDestroy[i].color = transparent;
        blocksToDestroy[i].combo = null;
        blocksToDestroy[i].isEmpty = true;
      }
    },
    animateCounter() {
      this.amountCounterClass = "";
      setTimeout(() => (this.amountCounterClass = "counter-zoom"), 100);
    },
    animateLevel() {
      this.levelCounterClass = "";
      setTimeout(() => (this.levelCounterClass = "counter-zoom"), 100);
    },
    createBombAtIndex(index) {
      this.blocks[index].color = transparent;
      this.blocks[index].combo = this.getBomb();
    },
    isFullColorBoard() {
      let color = this.blocks[0].color;
      return !this.blocks.find((b) => b.color != color);
    },
    findBlocksWithinBombArea(bombIndex, nextBlockIndex) {
      let rightBlock = this.blocks[nextBlockIndex + 1];
      let leftBlock = this.blocks[nextBlockIndex - 1];
      let topBlock = this.blocks[nextBlockIndex - columns];
      let bottomBlock = this.blocks[nextBlockIndex + columns];

      blocksToExplode.push(this.blocks[nextBlockIndex]);

      if (rightBlock) {
        if (
          this.isWithinBombArea(rightBlock.index, bombIndex, columns) &&
          !blocksToExplode.includes(rightBlock)
        ) {
          if (this.isCombo(rightBlock.combo)) {
            this.doCombo(rightBlock);
          } else {
            this.findBlocksWithinBombArea(bombIndex, rightBlock.index);
          }
        }
      }

      if (leftBlock) {
        if (
          this.isWithinBombArea(leftBlock.index, bombIndex, columns) &&
          !blocksToExplode.includes(leftBlock)
        ) {
          if (this.isCombo(leftBlock.combo)) {
            this.doCombo(leftBlock);
          } else {
            this.findBlocksWithinBombArea(bombIndex, leftBlock.index);
          }
        }
      }

      if (topBlock) {
        if (
          this.isWithinBombArea(topBlock.index, bombIndex, columns) &&
          !blocksToExplode.includes(topBlock)
        ) {
          if (this.isCombo(topBlock.combo)) {
            this.doCombo(topBlock);
          } else {
            this.findBlocksWithinBombArea(bombIndex, topBlock.index);
          }
        }
      }

      if (bottomBlock) {
        if (
          this.isWithinBombArea(bottomBlock.index, bombIndex, columns) &&
          !blocksToExplode.includes(bottomBlock)
        ) {
          if (this.isCombo(bottomBlock.combo)) {
            this.doCombo(bottomBlock);
          } else {
            this.findBlocksWithinBombArea(bombIndex, bottomBlock.index);
          }
        }
      }
    },
    findBlocksOnSameRow(block) {
      for (let i = 0; i <= columns; i++) {
        let rightBlock = this.blocks[block.index + i];
        if (!rightBlock) break;

        if (this.isSameRow(rightBlock.index, block.index, columns)) {
          if (blocksToExplode.includes(rightBlock)) continue;

          blocksToExplode.push(rightBlock);

          if (this.isCombo(rightBlock.combo)) {
            this.doCombo(rightBlock);
          }
        } else {
          break;
        }
      }
      for (let i = 0; i <= columns; i++) {
        let leftBlock = this.blocks[block.index - i];
        if (!leftBlock) break;

        if (this.isSameRow(leftBlock.index, block.index, columns)) {
          if (blocksToExplode.includes(leftBlock)) continue;

          blocksToExplode.push(leftBlock);

          if (this.isCombo(leftBlock.combo)) {
            this.doCombo(leftBlock);
          }
        } else {
          break;
        }
      }
    },
    findBlocksOnSameColumn(block) {
      for (let i = 0; i <= rows; i++) {
        let topBlock = this.blocks[block.index + columns * i];
        if (topBlock == null) break;

        if (blocksToExplode.includes(topBlock)) continue;

        blocksToExplode.push(topBlock);

        if (this.isCombo(topBlock.combo)) {
          this.doCombo(topBlock);
        }
      }
      for (let i = 0; i <= columns; i++) {
        let bottomBlock = this.blocks[block.index - columns * i];

        if (bottomBlock == null) break;

        if (blocksToExplode.includes(bottomBlock)) continue;

        blocksToExplode.push(bottomBlock);

        if (this.isCombo(bottomBlock.combo)) {
          this.doCombo(bottomBlock);
        }
      }
    },
    startMovingBlocks() {
      interval = setInterval(this.moveBlocks, 150);
    },
    moveBlocks() {
      let count = rows * columns;
      for (let i = count; i >= 0; i--) {
        let blockBelow = this.blocks[i + columns];

        if (!blockBelow) continue;

        if (blockBelow.color == transparent && (blockBelow.combo == null || blockBelow.combo == "")) {
          blockBelow.color = this.blocks[i].color;
          blockBelow.combo = this.blocks[i].combo;
          this.blocks[i].color = transparent;
          this.blocks[i].combo = null;
        }

        if (this.isFirstRow(i, columns) && this.blocks[i].color == transparent && (this.blocks[i].combo == null || this.blocks[i].combo == "")) {
          let randomColor = GameData.getRandomColor();
          this.blocks[i].color = randomColor;
        }
      }
    },
    createBoard() {
      for (let i = 0; i < rows * columns; i++) {
        let block = {
          index: i,
          color: transparent,
          combo: null
        };
        blocks.push(block);
      }
    },
    resetBoard() {
      for (let i = 0; i < this.blocks.length; i++) {
        blocks[i].color = transparent;
        blocks[i].combo = null;
      }
    },
    setGameRules() {
      this.addBonuses();
      this.amountCollected = 0;
      this.colorToCollect = GameData.getRandomColor();
      this.amountToCollect = this.level * 8;
      this.clicks = 20 + (this.clicks ? this.clicks : 0);
    },
    addBonuses() {
      if (this.level % 3 == 0)
        this.bonuses.find(b => b.bonus == this.getResetBonus()).amount++;
      if (this.level % 3 == 0)
        this.bonuses.find(b => b.bonus == this.getClickBonus()).amount++;
      if (this.level % 5 == 0) {
        let color = GameData.getRandomColor();
        this.bonuses.find(b => b.color == color).amount++;
        this.bonuses.find(b => b.color == color).amount++;
      }
    },
    startGame() {
      this.level = 1;
      this.playing = true;
      this.bonuses = this.getBonuses();

      if (blocks.length > 0) {
        this.resetBoard();
      } else {
        this.createBoard();
      }

      this.setGameRules();
      this.startMovingBlocks();
    },
  },
};
</script>

<style scoped>
.board {
  margin-top: 10px;
  display: grid;
  grid-template-columns: repeat(12, 25px);
  grid-template-rows: repeat(9, 25px);
  grid-column-gap: 2px;
  grid-row-gap: 2px;
}
.color-to-collect {
  width: 25px;
  height: 25px;
}
.block {
  border-radius: 5px;
}
.center-align-vertical {
  justify-content: center;
  align-items: center;
  text-align: center;
  display: flex;
  flex-direction: column;
}
hr {
  margin: 0px;
  padding: 0px;
  border-top: 1.5px solid #2c3e50;
}
p {
  margin: 0px;
  padding: 0px;
  font-size: 20px;
}
.counter-zoom {
  animation: zoom 0.4s ease-in;
}
@keyframes zoom {
  0% {
    transform: scale(1);
  }
  40% {
    transform: scale(1.5);
  }
  80% {
    transform: scale(0.7);
  }
  100% {
    transform: scale(1);
  }
}
.center-align-vertical {
  justify-content: center;
  align-items: center;
  text-align: center;
  display: flex;
  flex-direction: column;
}
</style>
