<template>
<div class="center-align-vertical">
        <div div class="screen">
            <div v-if="!playing">
              <br />
              <span  @click="startGame('snake')">
                <KeyCap letter='Snake' class="btn zoom" style="margin: 10px;"/>
              </span>
              <br />
              <span  @click="startGame('color')">
                <KeyCap letter='color puzzle' class="btn zoom" style="margin: 10px;"/>
              </span>
            </div>
            <div v-else class="game">
              <component v-bind:is="currentGame"/>
            </div>
        </div>
  <span @click="endGame" style="margin-top: 20px;">
    <KeyCap v-if="playing" letter='main menu' class="btn zoom"/>
  </span>
</div>
 
</template>

<script>
import ColorMatch from "@/components/games/colormatch/ColorMatch";
import Snake from "@/components/games/snake/Snake";
import KeyCap from '@/components/games/KeyCap';

export default {
  data() {
    return {
      playing: false,
      currentGame: null,
    };
  },
  components: {
    ColorMatch,
    Snake,
    KeyCap
  },
  methods: {
    startGame(game) {
      this.currentGame = game == "snake" ? Snake : ColorMatch;
      this.playing = true;
    },
    endGame(){
      this.playing = false;
    }
  },
};
</script>

<style scoped>
.screen {
  width: 310px;
  height: 425px;
}
.game {
  text-align: left;
  height: 100%;
  width: 100%;
}
.center-align-vertical {
  justify-content: center;
  align-items: center;
  text-align: center;
  display: flex;
  flex-direction: column;
}
</style>