
import GameData from '@/assets/gamedata/GameData';

const bomb = "fas fa-bomb";
    const columnCombo = "fas fa-arrows-alt-v";
    const rowCombo = "fas fa-arrows-alt-h";
    const resetBonus = "fas fa-sync-alt";
    const clickBonus = "fas fa-mouse";
    const fillBonus = "fas fa-tint";

export default {
    methods: {
        isBomb(combo) {
            return combo == bomb;
        },
        isColumnCombo(combo) {
            return combo == columnCombo;
        },
        isRowCombo(combo) {
            return combo == rowCombo;
        },
        isCombo(combo) {
            return combo == bomb || combo == columnCombo || combo == rowCombo;
        },
        getBomb() {
            return bomb;
        },
        getRowCombo() {
            return rowCombo;
        },
        getColumnCombo() {
            return columnCombo;
        },
        getBonuses() {
            let bonuses = [ { bonus: resetBonus, amount: 3, description: "reset the board", id: "0" },
            { bonus: clickBonus, amount: 1, description: "get 5 extra clicks", id: "1" },
            { bonus: fillBonus, amount: 1, description: "color the board green", color: GameData.colors[3], id: "2"},
            { bonus: fillBonus, amount: 1, description: "color the board pink", color: GameData.colors[2], id: "3"},
            { bonus: fillBonus, amount: 1, description: "color the board blue", color: GameData.colors[1], id: "4"},
            { bonus: fillBonus, amount: 1, description: "color the board gray", color: GameData.colors[0], id: "5"}]

            return bonuses;
        },
        isResetBonus(bonus) {
            return bonus == resetBonus;
        },
        isClickBonus(bonus) {
            return bonus == clickBonus;
        },
        getClickBonus(){
            return clickBonus;
        },
        getResetBonus(){
            return resetBonus;
        },
        getX(index, columns) {
            return parseInt(index % columns);
        },
        getY(index, columns) {
            return parseInt(index / columns);
        },
        isSameRow(value1, value2, columns) {
            return this.getRow(value1, columns) == this.getRow(value2, columns);
        },
        getRow(index, columns) {
            return Math.floor(index / columns);
        },
        isFirstRow(value, columns) {
            return value < columns;
        },
        isWithinBombArea(blockIndex, bombIndex, columns) {
            let xDistance = this.getX(blockIndex, columns) - this.getX(bombIndex, columns);
            let yDistance = this.getY(blockIndex, columns) - this.getY(bombIndex, columns);

            let Distance = Math.sqrt(xDistance * xDistance + yDistance * yDistance);

            return Distance <= 2;
        },
        isThreeInAColumn(blocks)
        {
            blocks = blocks.sort(function (a, b) {
                return a.index - b.index;
              });

            let blockCount = blocks.length;
            for (let i = 0; i < blockCount; i++)
            {
                if (i + 1 >= blockCount) break;
                if (blocks[i + 1].index - blocks[i].index != 1) return false;
            }
    
            return true;
        },
        isThreeInARow(blocks, columns)
        {
            blocks = blocks.sort(function (a, b) {
                return a.index - b.index;
              });

            let blockCount = blocks.length;
            for (let i = 0; i < blockCount; i++)
            {
                if (i + 1 >= blockCount) break; 
                if (blocks[i + 1].index - blocks[i].index != columns) return false;
            }
    
            return true;
        }
    }
}