var GameData = {
    colors: ["slategray", "lightblue", "lightpink", "lightgreen"],
    getColors() {
        return GameData.colors;
    },
    getRandomColor() {
        return GameData.colors[GameData.getRandomNumber(0, GameData.colors.length)];
    },
    getRandomNumber(min, max) {
        return Math.floor(Math.random() * (max - min) + min);
    }
}

export default GameData;
